import { useState } from "react";
export default function FAQ() {
  const [faqs, setFaqs] = useState([
    {
      id: 1,
      q: "Where can I get this HTML template?",
      s: "You can download it on Gumroad.com",
      open: false,
    },
    {
      id: 2,
      q: "Is this HTML template free?",
      s: "Yes! For you it is free.",
      open: false,
    },
    { id: 3, q: "Am I awesome?", s: "Yes! No doubt about it.", open: false },
    { id: 4, q: "Question Template?", s: "Answer Template", open: false },
  ]);
  return (
    <section class="w-full items-start pt-8 bg-white text-black">
      <div>
        <h2 class="text-5xl bg-left-bottom bg-no-repeat pb-8 mb-8 w-full p-10 mb-0 bg-center bg-100%">
          FAQs
        </h2>
      </div>
      <div class="w-11/12 mx-auto">
        {faqs.map(({ id, q, s, open }) => {
          return (
            <div key={`Key-${id}`}>
              <div
                toggleElement
                onClick={() => {
                  if (open) {
                    let fas = [];
                    faqs.map((val) => {
                      fas.push({ id: val.id, q: val.q, s: val.s, open: false });
                    });
                    setFaqs(fas);
                  } else {
                    let fas = [];
                    faqs.map((val) => {
                      if (id === val.id) {
                        fas.push({
                          id: val.id,
                          q: val.q,
                          s: val.s,
                          open: true,
                        });
                      } else {
                        fas.push({
                          id: val.id,
                          q: val.q,
                          s: val.s,
                          open: false,
                        });
                      }
                    });
                    setFaqs(fas);
                  }
                }}
                class="w-full py-4">
                <div class="flex justify-between items-center">
                  <div class="font-montserrat font-medium mr-auto">{q}</div>
                  <img
                    src={require("../assets/img/CaretRight.svg").default}
                    alt=""
                    class={
                      open
                        ? "transform transition-transform rotate-90"
                        : "transform transition-transform"
                    }
                  />
                </div>
                <div
                  class={
                    open
                      ? "font-montserrat text-sm text-left font-extralight pb-8"
                      : "font-montserrat text-sm text-left font-extralight pb-8 hidden"
                  }>
                  {s}
                </div>
              </div>
              <hr class="w-full bg-white" />
            </div>
          );
        })}
      </div>
      <div class="bg-primary h-0.5 mx-auto my-12 w-6/12" />
    </section>
  );
}
